<template>
  <v-container class="reportIndex" fluid>
    <!-- 當前報表狀態載入動態圖 -->
    <ReportStatusImage
      v-if="reportStatus"
      :reportStatus="reportStatus"
    />
    <div v-else>
      <!-- 載入中 -->
      <div
        v-show="loadingStatus"
        class="text-xs-center">
        <MaterialLoadingPage/>
      </div>
      <div v-show="!loadingStatus">
        <!-- 體彩無比分的場數 -->
        <div v-if="!isEmpty(notCheckedSports)">
          <h4 class="mt-0">{{ $ln('目前未派彩') }}</h4>
          <div class="scrollbar">
            <v-chip
              v-for="(value, key) in notCheckedSports"
              :key="key"
              class="ma-2"
              color="grey lighten-3">
              {{ key }}  <span class="red--text px-1">{{ value }}</span>{{ $ln('場') }}
            </v-chip>
          </div>
        </div>

        <!-- 標題 & 查詢報表按鈕 -->
        <v-layout
          wrap
          row
          class="px-2 pt-1 pb-1">
          <v-flex
            md8
            :xs6="!viMobileStatus"
            :xs12="viMobileStatus"
            tag="h3"
            class="mt-0">
            {{ $ln('報表(時間)') }}
          </v-flex>
          <v-flex
            md4
            :xs6="!viMobileStatus"
            :xs12="viMobileStatus"
            :class="[!viMobileStatus ? 'text-xs-right' : 'text-xs-left']">
            <v-btn
              small
              class="ma-2"
              color="deep-orange darken-1"
              :disabled="!searchAccountItems.valid"
              @click="search()">
              {{ $ln('查詢報表') }}
            </v-btn>
          </v-flex>
        </v-layout>

        <!-- 勾選項目 -->
        <material-card>
          <v-form v-model="reportItems.valid">
            <!-- 投注時間 -->
            <v-layout
              wrap
              row
              class="pb-2">
              <v-flex
                xl1
                md2
                class="pt-3">
                <label>{{ $ln('投注時間') }}</label>
              </v-flex>

              <v-flex
                md6
                sm12
                xs12>
                <!-- 日期 -->
                <!-- <CommonDatePicker
                  Title=""
                  :StartTime.sync="reportItems.countDate"
                  :EndTime.sync="reportItems.countDateEnd"
                /> -->
                <v-layout
                  warp
                  row>
                  <v-flex
                    xs5>
                    <div class="v-input v-text-field v-input--is-label-active v-input--is-dirty theme--light">
                      <div class="v-input__control">
                        <div class="v-input__slot">
                          <div class="v-text-field__slot">
                            <input
                              v-model="reportItems.countDate"
                              name="start-datetime"
                              type="text"
                              class="datetime-picker form-control"
                              :placeholder="$ln('開始時間')">
                          </div>
                        </div>
                        <div class="v-text-field__details">
                          <div class="v-messages theme--light">
                            <div class="v-messages__wrapper"/>
                          </div>
                        </div>
                      </div>
                    </div>
                  </v-flex>
                  <v-flex
                    xs2
                    class="pt-4 px-0 text-xs-center">
                    ~
                  </v-flex>
                  <v-flex
                    xs5>
                    <div class="v-input v-text-field v-input--is-label-active v-input--is-dirty theme--light">
                      <div class="v-input__control">
                        <div class="v-input__slot">
                          <div class="v-text-field__slot">
                            <input
                              v-model="reportItems.countDateEnd"
                              name="end-datetime"
                              type="text"
                              class="datetime-picker form-control"
                              :placeholder="$ln('結束時間')">
                          </div>
                        </div>
                        <div class="v-text-field__details">
                          <div class="v-messages theme--light">
                            <div class="v-messages__wrapper"/>
                          </div>
                        </div>
                      </div>
                    </div>
                  </v-flex>
                </v-layout>
              </v-flex>
            </v-layout>

            <!-- 館別/項目 -->
            <v-layout
              wrap
              row
              class="pb-2">
              <v-flex
                xl1
                lg2
                md2>
                <label>{{ $ln('館別/項目') }}</label>
                <v-tooltip top>
                  <v-icon
                    slot="activator"
                    color="amber">mdi-information-outline</v-icon>
                  <span>{{ $ln('必須勾選【館別/項目】選項才能搜尋報表。若有勾選「Super彩球」和其他遊戲，則預設其他遊戲的搜尋結果') }}</span>
                </v-tooltip>
                <v-checkbox
                  class="v-input-checkbox"
                  v-model="reportItems.typeSelectAll"
                  :label="$ln('全選')"
                />
              </v-flex>

              <v-flex
                xl11
                lg10
                md10>
                <!-- 體彩 -->
                <CommonCheckbox
                  :TypeItems.sync="reportItems.ballTypeItems"
                  :TypeSelectAll.sync="ballTypeSelectAll"
                  TypeSelectAllName="體彩全部"
                />
                <!-- BingoStar -->
                <CommonCheckbox
                  :TypeItems.sync="reportItems.bgTypeItems"
                  :TypeSelectAll.sync="bgTypeSelectAll"
                  TypeSelectAllName="BingoStar 全部"
                  />
                <!-- Super彩球 -->
                <CommonCheckbox
                  :TypeItems.sync="reportItems.ltTypeItems"
                  :TypeSelectAll.sync="ltTypeSelectAll"
                  TypeSelectAllName="Super彩球全部"
                />
                <!-- 皇家 -->
                <CommonCheckbox
                  :TypeItems.sync="reportItems.ryTypeItems"
                  :TypeSelectAll.sync="ryTypeSelectAll"
                  TypeSelectAllName="皇家全部"
                />
              </v-flex>
            </v-layout>

            <!-- 類型 -->
            <v-layout
              wrap
              row
              class="pb-2">
              <v-flex
                xl1
                lg2
                md2>
                <label>{{ $ln('類型') }}</label>
                <v-checkbox
                  class="v-input-checkbox"
                  v-model="reportItems.gameTypeSelectAll"
                  :label="$ln('全選')"
                />
              </v-flex>

              <v-flex
                xl11
                lg10
                md10>
                <!-- 體彩 -->
                <CommonCheckbox
                  :TypeItems.sync="reportItems.ballGameTypeItems"
                  :TypeSelectAll.sync="ballGameTypeSelectAll"
                  TypeSelectAllName="體彩全部"
                />
                <!-- BingoStar -->
                <CommonCheckbox
                  :TypeItems.sync="reportItems.bgGameTypeItems"
                  :TypeSelectAll.sync="reportItems.bgGameTypeItems.selected"
                  TypeSelectAllName="BingoStar全部 (同館別/項目)"
                />
                <!-- Super彩球 -->
                <CommonCheckbox
                  :TypeItems.sync="reportItems.ltGameTypeItems"
                  :TypeSelectAll.sync="reportItems.ltGameTypeItems.selected"
                  TypeSelectAllName="Super彩球全部 (同館別/項目)"
                />
                <!-- 皇家 -->
                <CommonCheckbox
                  :TypeItems.sync="reportItems.ryGameTypeItems"
                  :TypeSelectAll.sync="reportItems.ryGameTypeItems.selected"
                  TypeSelectAllName="皇家全部 (同館別/項目)"
                />
              </v-flex>
            </v-layout>

            <!-- 玩法 -->
            <v-layout
              wrap
              row>
              <v-flex
                xl1
                lg2
                md2>
                <label>{{ $ln('玩法') }}</label>
                <v-checkbox
                  class="v-input-checkbox"
                  v-model="reportItems.playTypeSelectAll"
                  :label="$ln('全選')"
                />
              </v-flex>

              <v-flex
                xl11
                lg10
                md10>
                <!-- 體彩 -->
                <CommonCheckbox
                  :TypeItems.sync="reportItems.ballPlayTypeItems"
                  :TypeSelectAll.sync="ballPlayTypeSelectAll"
                  TypeSelectAllName="體彩全部"
                />
                <!-- BingoStar -->
                <CommonCheckbox
                  :TypeItems.sync="reportItems.bgPlayTypeItems"
                  :TypeSelectAll.sync="reportItems.bgPlayTypeItems.selected"
                  TypeSelectAllName="BingoStar全部 (同館別/項目)"
                />
                <!-- Super彩球 -->
                <CommonCheckbox
                  :TypeItems.sync="reportItems.ltPlayTypeItems"
                  :TypeSelectAll.sync="reportItems.ltPlayTypeItems.selected"
                  TypeSelectAllName="Super彩球全部 (同館別/項目)"
                />
                <!-- 皇家 -->
                <CommonCheckbox
                  :TypeItems.sync="reportItems.ryPlayTypeItems"
                  :TypeSelectAll.sync="reportItems.ryPlayTypeItems.selected"
                  TypeSelectAllName="皇家全部 (同館別/項目)"
                />
              </v-flex>
            </v-layout>

            <!-- 查詢帳號 -->
            <v-form v-model="searchAccountItems.valid">
              <v-layout
                wrap
                row>
                <v-flex
                  xl1
                  md2>
                  <label>{{ $ln('查詢帳號') }}</label>
                </v-flex>
                <v-flex
                  xl3
                  md2
                  xs12>
                  <v-text-field
                    v-model="reportItems.account"
                    :rules="searchAccountItems.rules"
                    required
                  />
                </v-flex>
              </v-layout>
            </v-form>
          </v-form>
        </material-card>

        <!-- 底部查詢報表按鈕 -->
        <div :class="['px-2', viMobileStatus ? 'text-xs-left' : 'text-xs-right']">
          <v-btn
            small
            class="ma-2"
            color="deep-orange darken-1"
            :disabled="!searchAccountItems.valid"
            @click="search()">
            {{ $ln('查詢報表') }}
          </v-btn>
        </div>
      </div>
    </div>
  </v-container>
</template>

<script>
